import { isNaN } from 'lodash';

export const ROLES = ['responsible', 'accountable', 'consulted', 'informed'];

const getArrayUser = action => {
  return Array.from(ROLES).reduce((acc, key) => {
    if (action[key]) {
      acc.push(action[key].toUpperCase());
    }
    return acc;
  }, []);
};

export const getUniqueUsers = actions => {
  let userActions = [];
  actions.forEach(action => {
    const arrayUser = getArrayUser(action);
    userActions = [...userActions, ...arrayUser];
    if (action?.subActions) {
      const subUser = getUniqueUsers(action.subActions);
      userActions = [...userActions, ...subUser];
    }
  });
  return [...new Set(userActions)];
};

export const isValidDate = date => {
  return date instanceof Date && !isNaN(date.getTime());
};

export const equalsIgnoreCase = (s1, s2) => {
  return `${s1}`.toUpperCase() === `${s2}`.toUpperCase();
};
